<template>
  <div class="bookrack">
    <div v-if="isShow" class="bookrack-content content-1">
      <div class="imgBox">
        <img class="auto-img" src="../../assets/images/books.png" alt="图片未加载..." />
      </div>
      <p class="text">把时间花在美好的事物上</p>
      <p class="text">让阅读变成一种习惯</p>
      <p class="text">去书城逛逛吧</p>
    </div>
    <div v-else class="bookrack-content clearfix">
      <div
        class="book-box fl"
        @click="goBook(item._id)"
        v-for="(item,index) in bookList"
        :key="index"
      >
        <div class="img-box">
          <img :src="getImg(item.cover)" alt="图片加载ing..." />
        </div>
        <div class="text">{{item.title}}</div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations } = createNamespacedHelpers("saveDataFn");
export default {
  data() {
    return {
      isShow: true,
      idList: [],
      bookList: []
    };
  },
  created() {
    if (this.bookCache.length > 0) {
      this.isShow = false;
      this.idList = this.bookCache;
      this.getBook();
    }
  },
  methods: {
    // 获取加入书架的数据
    getBook() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      for (let i = 0; i < this.idList.length; i++) {
        this.axios({
          method: "get",
          url: "http://novel.kele8.cn/book-info/" + this.idList[i]
        })
          .then(res => {
            this.$toast.clear();
 
            if (res.status == 200) {
              this.bookList.push(res.data);
            }
          })
          .catch(err => {
            this.$toast.clear();
 
          });
      }
    },
    getImg(val) {
      return unescape(val).slice(7);
    },
    goBook(id) {
      this.$router.push({ name: "Book", query: { id } });
    }
  },
  computed: {
    ...mapState(["bookCache"])
  }
};
</script>

<style lang="less" scoped>
// 底部导航样式
/deep/.van-tabbar-item--active {
  color: #ccc;
}
// 书架样式
.bookrack {
  width: 100%;
  height: 100%;
  position: fixed;
  // 内容公共样式
  .bookrack-content {
    font-size: 16px;
    padding-bottom: 50px;
    height: 100%;
    padding: 15px 20px;
    .imgBox {
      width: 144px;
      height: 144px;
      margin: 160px auto 0;
    }
    .text {
      text-align: center;
      color: lightsteelblue;
      font-size: 18px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .book-box {
      margin: 0 auto;
      padding: 5px;
      box-sizing: border-box;
      width: 30%;
      height: 150px;
      border-radius: 5px;
      box-shadow: 1px 1px 5px 1px #ddd;
      margin: 0 3% 15px 0;
      .img-box {
        width: 100%;
        height: 120px;
        img {
          height: 100%;
        }
      }
    }
  }
}
</style>